import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, MetaDataType } from 'shared-code-lib';
import { Pop3Agent } from '../../../blocks/models/importer';

@Component({
  selector: 'app-agent-pop3',
  templateUrl: './agent-pop3.component.html',
  styleUrls: ['./agent-pop3.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AgentPop3Component implements OnInit {
  @Input() agent: Pop3Agent;

  fieldHost: FieldDefinition;
  fieldPort: FieldDefinition;
  fieldUsername: FieldDefinition;
  fieldPassword: FieldDefinition;
  attachmentFilter: FieldDefinition;

  get agentPort() {
    return `${this.agent.port}`;
  }
  set agentPort(val: string) {
    this.agent.port = Number(val);
  }

  constructor(public translate: TranslateService) {}

  ngOnInit() {
    this.createFields();
  }

  private createFields = () => {
    this.fieldHost = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-importer.host'),
      placeholder: this.translate.instant('admin-importer.host-placeholder'),
      required: true,
    });

    this.fieldPort = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translate.instant('admin-importer.port'),
      placeholder: this.translate.instant('admin-importer.port-placeholder'),
      required: true,
    });

    this.fieldUsername = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-importer.username'),
      placeholder: this.translate.instant('admin-importer.username-placeholder'),
      required: true,
    });

    this.fieldPassword = new FieldDefinition({
      type: MetaDataType.Password,
      label: this.translate.instant('admin-importer.password'),
      placeholder: this.translate.instant('admin-importer.password-placeholder'),
      required: false,
    });

    this.attachmentFilter = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-importer.attachment-filter'),
      placeholder: this.translate.instant('admin-importer.attachment-filter-placeholder'),
      required: false,
    });
  };
}
