import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdminImporterComponent } from './admin-importer.component';

const routes: Routes = [
  {
    path: 'duplicate/:importerid',
    component: AdminImporterComponent,
    data: { type: 'duplicate' },
    pathMatch: 'full',
  },
  {
    path: 'create/:importertype',
    component: AdminImporterComponent,
    data: { type: 'create' },
    pathMatch: 'full',
  },
  {
    path: ':importerid',
    component: AdminImporterComponent,
    data: { type: 'properties' },
    pathMatch: 'full',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AdminImporterRoutingModule {}
