import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Data, Params, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Importer, ImporterAgent, InterFAXAgent, Pop3Agent } from '../../blocks/models/importer';
import { WorkflowsDataSource } from '../../blocks/models/workflow-datasource';
import { ErrorsService } from '../../blocks/services/errors.service';
import { HydraApiService } from '../../blocks/services/hydra-api.service';
import { ApiResult, Control } from '../../blocks/services/models';
import { SpinnerStateService } from '../../blocks/services/spinner-state.service';

import { Subscription, combineLatest } from 'rxjs';
import { FieldDefinition, MetaDataType } from 'shared-code-lib';

@Component({
  selector: 'app-admin-importer',
  templateUrl: './admin-importer.component.html',
  styleUrls: ['./admin-importer.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AdminImporterComponent implements OnInit, OnDestroy {
  routeParams: Params;
  routeData: Data;

  importer = new Importer();

  saveButton: Control;
  cancelButton: Control;
  formControls: Array<Control> = [];

  fieldName: FieldDefinition;
  fieldWorkflows: FieldDefinition;

  workflowsDataSource: WorkflowsDataSource;

  subscriptions = new Subscription();

  constructor(
    private translate: TranslateService,
    private router: Router,
    private route: ActivatedRoute,
    private hydraApi: HydraApiService,
    private toastr: ToastrService,
    private errorsService: ErrorsService,
    private spinner: SpinnerStateService
  ) {}

  ngOnInit() {
    this.createButtons();

    this.subscriptions.add(
      combineLatest([this.route.params, this.route.data]).subscribe(([params, data]) => {
        this.routeParams = params;
        this.routeData = data;
        this.loadImporter();
      })
    );

    this.hydraApi.getWorkflows(
      (workflows) => {
        this.workflowsDataSource = new WorkflowsDataSource(workflows);

        this.createFields();
      },
      (err: any) => {
        this.toastr.error(this.translate.instant('errors.failed-load-workflows'));
      }
    );
  }

  onTestConnection = (): void => {
    this.spinner.showSavingSpinner('admin-importer.verifying-connectivity');

    this.hydraApi.testImporterAgentConnectivity(
      this.importer,
      this.importerConnectivityVerified,
      this.importerConnectivityFailed
    );
  };

  private importerConnectivityVerified = () => {
    this.toastr.success(this.translate.instant('admin-importer.connectivity-succeeded-verify'));

    this.spinner.hide();
  };

  private importerConnectivityFailed = (err: any) => {
    this.toastr.error(this.translate.instant('admin-importer.connectivity-failed-verify'));

    this.spinner.hide();
  };

  private loadImporter = () => {
    if (this.routeData.type === 'properties' || this.routeData.type === 'duplicate') {
      this.hydraApi.getImporter(this.routeParams.importerid, this.importerLoaded, this.importerLoadFailed);
    } else if (this.routeData.type == 'create') {
      this.importer.agent = ImporterAgent.Create(this.routeParams.importertype);
    }

    this.importerLoaded(this.importer);
  };

  private importerLoaded = (i: Importer) => {
    if (this.routeData.type === 'duplicate') {
      i.name = `${this.translate.instant('Admin.CopyOf')} ${i.name}`;
      i.id = '';
    }

    this.importer = i;
  };

  private importerLoadFailed = () => {
    this.toastr.error(this.translate.instant('Importer-Form.load-failed'));
  };

  private createFields = () => {
    this.fieldName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-importer.name'),
      placeholder: this.translate.instant('admin-importer.name-placeholder'),
      required: true,
    });

    this.fieldWorkflows = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('admin-importer.workflow'),
      placeholder: this.translate.instant('admin-importer.workflow'),
      datasource: this.workflowsDataSource,
      required: true,
    });
  };

  private createButtons() {
    this.saveButton = new Control(
      this.onSave,
      this.translate.instant('admin-importer.save'),
      'btn btn-primary btn-minwidth',
      '',
      '',
      'btn',
      false
    );
    this.cancelButton = new Control(
      this.onCancel,
      this.translate.instant('admin-importer.cancel'),
      'btn btn-light btn-minwidth',
      '',
      '',
      'btn',
      false
    );

    this.formControls.push(this.saveButton);
    this.formControls.push(this.cancelButton);
  }

  onSave = () => {
    if (this.importer.id) {
      this.hydraApi.updateImporter(this.importer, this.importerSaved, this.importerSaveFailed);
    } else {
      this.hydraApi.createImporter(this.importer, this.importerSaved, this.importerSaveFailed);
    }
    this.spinner.showSavingSpinner();
  };

  importerSaved = (result: ApiResult) => {
    if (result.success) {
      this.toastr.success(this.translate.instant('admin-importer.saved'));
      this.returnToGrid();
    } else {
      this.errorsService.showErrorsModal(result.errors, 'admin-importer.validation-errors');
    }

    this.spinner.hide();
  };

  importerSaveFailed = (err: any) => {
    this.toastr.error(this.translate.instant('admin-importer.save-failed'));
    this.spinner.hide();
  };

  onCancel = () => {
    this.returnToGrid();
  };

  returnToGrid = () => {
    this.router.navigateByUrl(
      'settings/BambaSettingsApplication/BambaSettingsApplication-Upland.Bamba.Application.Settings.Importers'
    ); // todo - handle this "better"
  };

  getInterFAXAgent = () => this.importer.agent as InterFAXAgent;
  getPop3Agent = () => this.importer.agent as Pop3Agent;

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
