import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Importer, Office365Agent } from '../../../blocks/models/importer';

import { Subject } from 'rxjs';
import { FieldDefinition, IFieldDataSource, IFieldOptions, MetaDataType } from 'shared-code-lib';
import { IRadioButtonOption } from '../../../../../../../Bamba.Shared/src/models/radio-buttons-group';
import { HydraApiService } from '../../../blocks/services/hydra-api.service';
import { ApiResult } from '../../../blocks/services/models';

@Component({
  selector: 'app-agent-office365',
  templateUrl: './agent-office365.component.html',
  styleUrls: ['./agent-office365.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AgentOffice365Component implements OnInit {
  _importer: Importer;

  @Input() set importer(importer: Importer) {
    this._importer = importer;
    this.agent = this._importer.agent as Office365Agent;
  }
  get importer(): Importer {
    return this._importer;
  }

  agent: Office365Agent;

  fieldUsername: FieldDefinition;
  fieldTenantId: FieldDefinition;
  fieldClientId: FieldDefinition;
  fieldClientSecret: FieldDefinition;
  fromFolder: FieldDefinition;
  toFolder: FieldDefinition;
  attachmentFilter: FieldDefinition;
  agentFromFolderOptions: IRadioButtonOption<string>[] = [];
  agentToFolderOptions: IRadioButtonOption<string>[] = [];

  folderDataSource: ImporterFolderDataSource;

  constructor(public translate: TranslateService, private hydraApi: HydraApiService) {}

  ngOnInit() {
    this.createFields();
  }

  private createFields = () => {
    this.folderDataSource = new ImporterFolderDataSource(this.importer, this.hydraApi);

    this.fieldUsername = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-importer.username'),
      placeholder: this.translate.instant('admin-importer.username-placeholder'),
      required: true,
    });

    this.fieldTenantId = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-importer.tenant-id'),
      placeholder: this.translate.instant('admin-importer.tenant-id-placeholder'),
      required: true,
    });

    this.fieldClientId = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-importer.client-id'),
      placeholder: this.translate.instant('admin-importer.client-id-placeholder'),
      required: true,
    });

    this.fieldClientSecret = new FieldDefinition({
      type: MetaDataType.Password,
      label: this.translate.instant('admin-importer.client-secret'),
      placeholder: this.translate.instant('admin-importer.client-secret-placeholder'),
      required: true,
    });

    this.fromFolder = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('admin-importer.from-folder'),
      placeholder: this.translate.instant('admin-importer.from-folder-placeholder'),
      datasource: this.folderDataSource,
      required: true,
    });

    this.toFolder = new FieldDefinition({
      type: MetaDataType.List,
      datasource: this.folderDataSource,
      label: this.translate.instant('admin-importer.to-folder'),
      placeholder: this.translate.instant('admin-importer.to-folder-placeholder'),
      required: true,
    });

    this.agentFromFolderOptions = [
      {
        label: this.translate.instant('admin-importer.from-folder-default'),
        value: null,
      },
      {
        label: this.translate.instant('admin-importer.from-folder-custom'),
        value: this.agent?.fromFolder ?? '',
      },
    ];

    this.agentToFolderOptions = [
      {
        label: this.translate.instant('admin-importer.to-folder-default'),
        value: null,
      },
      {
        label: this.translate.instant('admin-importer.to-folder-custom'),
        value: this.agent?.toFolder ?? '',
      },
    ];

    this.attachmentFilter = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-importer.attachment-filter'),
      placeholder: this.translate.instant('admin-importer.attachment-filter-placeholder'),
      required: false,
    });
  };

  onPropertyChanged() {
    this.folderDataSource.changed.next();
  }

  onFromFolderChanged = (value: string): void => {
    this.agentFromFolderOptions[1].value = value;
  };

  onToFolderChanged = (value: string): void => {
    this.agentToFolderOptions[1].value = value;
  };
}

class ImporterFolderDataSource implements IFieldDataSource {
  changed = new Subject<void>();

  constructor(public importer: Importer, private hydraApi: HydraApiService) {}

  getItems(
    startsWith: string,
    currentValue: string,
    successCallBack: (items: IFieldOptions[]) => void,
    failureCallBack: (err: any) => void
  ): void {
    const agent = this.importer.agent as Office365Agent;
    if (!agent.username || !agent.tenantId || !agent.clientId || !agent.clientSecret) {
      successCallBack([]);
      return;
    }

    this.hydraApi.loadImporterFolders(
      this.importer,
      (folders: ApiResult) => {
        if (folders.success) {
          const items = new Array<IFieldOptions>();

          for (const folder of folders.object as Array<string>) {
            const item = { name: folder, value: folder };
            items.push(item);
          }

          successCallBack(items);
        } else {
          failureCallBack(folders.errors);
        }
      },
      failureCallBack
    );
  }
}
